// src/components/Dashboard/Statistics.js

import React, { useState, useEffect, useContext } from 'react';
import { Bar } from 'react-chartjs-2';
import { Chart as ChartJS, CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend } from 'chart.js';
import axios from 'axios';
import { useAuth } from '../../hooks/useAuth';  // Custom hook for authentication

ChartJS.register(CategoryScale, LinearScale, BarElement, Title, Tooltip, Legend);

const Statistics = () => {
  const { token, user } = useAuth();  // Use the useAuth hook to get token and user

  const [pollutionData, setPollutionData] = useState([]);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchPollutionStats = async () => {
      try {
        setLoading(true);
        const response = await axios.get('/api/pollution/stats', {
          headers: {
            Authorization: `Bearer ${token}`,  // Authorization using JWT token
          },
        });

        // Check if response data exists and is valid
        if (response.data && response.data.stats) {
          setPollutionData(response.data.stats);
        } else {
          throw new Error('No data available');
        }
      } catch (error) {
        console.error('Error fetching pollution statistics:', error);
        setError('Unable to load statistics. Please try again later.');
      } finally {
        setLoading(false);
      }
    };

    fetchPollutionStats();
  }, [token]);

  const processChartData = () => {
    const labels = pollutionData.map(item => item.month);
    const data = pollutionData.map(item => item.incidents);

    return {
      labels,
      datasets: [
        {
          label: 'Pollution Incidents',
          data: data,
          backgroundColor: 'rgba(54, 162, 235, 0.6)',
          borderColor: 'rgba(54, 162, 235, 1)',
          borderWidth: 1,
          hoverBackgroundColor: 'rgba(54, 162, 235, 0.8)',
        },
      ],
    };
  };

  const chartOptions = {
    responsive: true,
    plugins: {
      legend: {
        position: 'top',
      },
      title: {
        display: true,
        text: `Monthly Pollution Statistics (${new Date().getFullYear()})`,
      },
    },
    scales: {
      y: {
        beginAtZero: true,
        ticks: {
          stepSize: 1,
        },
      },
    },
  };

  if (loading) {
    return <p>Loading pollution statistics...</p>;
  }

  if (error) {
    return <p className="error">{error}</p>;
  }

  return (
    <div className="statistics-container">
      <h2>Pollution Statistics Dashboard</h2>
      {pollutionData.length ? (
        <Bar data={processChartData()} options={chartOptions} />
      ) : (
        <p>No data available for the current period.</p>
      )}
    </div>
  );
};

export default Statistics;
