import React from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import { AuthProvider } from './context/AuthContext'; // Ensure the path is correct
import Header from './components/Header';
import Footer from './components/Footer';
import Home from './components/Home';
import Login from './components/Auth/Login';
import Register from './components/Auth/Register';
import ReportPollution from './components/Pollution/ReportPollution';
import PollutionMap from './components/Pollution/PollutionMap';
import Statistics from './components/Dashboard/Statistics';
import PrivateRoute from './components/PrivateRoute';
import './App.css';

const App = () => {
    return (
        <AuthProvider>
            <Router>
                <Header />
                <div className="container">
                    <Routes>
                        {/* Corrected URL paths */}
                        <Route path="/" element={<Home />} />  {'mobile_app\marine_restoration_app\src\components\Home.js'}
                        <Route path="/login" element={<Login />} />  {'mobile_app\marine_restoration_app\src\components\Auth\Login.js'}
                        <Route path="/register" element={<Register />} />  {'mobile_app\marine_restoration_app\src\components\Auth\Register.js'}
                        
                        {/* Protected Routes (Requires authentication) */}
                        <Route path="/report" element={<PrivateRoute><ReportPollution /></PrivateRoute>} />
                        <Route path="/map" element={<PrivateRoute><PollutionMap /></PrivateRoute>} />
                        <Route path="/statistics" element={<PrivateRoute><Statistics /></PrivateRoute>} />

                        {/* Catch-all for undefined routes */}
                        <Route path="*" element={<h2 className="text-center mt-2">404 - Page Not Found</h2>} />
                    </Routes>
                </div>
                <Footer />
            </Router>
        </AuthProvider>
    );
};

export default App;
