import React, { useState } from 'react';
import axios from 'axios';

const ReportPollution = () => {
    const [location, setLocation] = useState('');
    const [severity, setSeverity] = useState('');
    
    const handleSubmit = async (e) => {
        e.preventDefault();
        const report = { location, severity };

        try {
            const response = await axios.post('/api/pollution', report);
            console.log('Pollution report submitted:', response.data);
        } catch (error) {
            console.error('Error reporting pollution:', error);
        }
    };

    return (
        <form onSubmit={handleSubmit}>
            <label>
                Location:
                <input type="text" value={location} onChange={e => setLocation(e.target.value)} />
            </label>
            <label>
                Severity:
                <input type="number" value={severity} onChange={e => setSeverity(e.target.value)} />
            </label>
            <button type="submit">Submit Report</button>
        </form>
    );
};

export default ReportPollution;
